	module.exports = {"data":{"nodePost":{"title":"World wide wrist - Ethan Marcotte","link":"https://ethanmarcotte.com/wrote/wwwatch/","body":{"value":"<blockquote>\r\n<a href=\"https://twitter.com/jensimmons/status/1003703375656087553\">Jen Simmons’s suggestion</a> is spot-on: it just got a <em>lot</em> more important to design from a sensible, small screen-friendly document structure built atop semantic HTML.\r\n</blockquote>\r\n<p>I'm less excited about WebKit coming to watchOS than I am about the overall impact it will have. The more we focus on developing experiences that can be consumed at any size on any fidelity device, the better the web will become.</p>\r\n<p>Nice <a href=\"https://erikrunyon.com/2018/06/designing-web-content-for-watchos/\">post from Erik Runyon about some of the technical specifics</a> as well.</p>"},"created":1528296208}},"pathContext":{"slug":"/link/world-wide-wrist-ethan-marcotte/"}}

